.sidebarOption {
  display: flex;
  align-items: center;
  color: grey;
  cursor: pointer;
  height: 40px;
  transition: 200ms color ease-in;
}

.sidebarOption:hover {
  color: #fff;
}

.sidebarOption__icon {
    padding-right: 10px;
    padding-left: 10px;
}

.sidebarOption > p{
    margin-top: 10px;
    margin-left: 20px;
    font-size: 14px;
}
